.blog{
    @import '../variables/variables';
    @import '../components/fonts';
    @import '../components/media';
    @import '../components/colors';
    background: $gray-news;
    p{
        color: $black-dark;
        margin-bottom: 24px;
        @include media(sm){
            margin-bottom: 20px;
        }
        
    }
    a{
        color: $red;
    }

    strong{
        color: $black-dark;
        font-weight: 600 !important;
    }
    &.block-portada-articulo{
        padding: 30px 0;
        .row-card{
            background: linear-gradient(to right, white 64%, $red 36%);
            grid-gap: 0;
            @include media(md){
                background: linear-gradient(to bottom, white 64%, $red 36%);
             
            }
            > div{
                align-content: center;
                justify-items: center;
            }
            .col-content{
                padding-left: 84px;
                padding-right: .5rem;
                padding-top: 2rem;
                padding-bottom: 2rem;
                @include media(md){
                    padding: 1.5rem;
                }
                @include media(sm){
                    padding: 1rem;
                }
            }
            .col-image{
                padding: 28px 0;
                padding-left: 4rem;
                @include media(md){
                    padding: 1.5rem;
               
                }
                @include media(sm){
                    padding: 1rem;
              
                }
                .image{
                    display: flex;
                    width: 100%;
                    overflow: hidden;
                    @include aspectRatio();
                    height: 600px;
                    @include media(md){

                        height: 500px;
                    }
                    @include media(sm){

                        height: 400px;
                    }
                    @include media(xs){
                        height: 250px;
                    }
                    picture,img{
                        object-fit: cover;
                        object-position: center;
                    }
                }
            }
            .content{
                display: flex;
                flex-direction: column;
                a{
                    color: $breadcrum;
                    font-size: 14px;
                    margin-bottom: 22px;
                    @include media(sm){
                        font-size: 12px;
                        margin-bottom: .5rem;
                    }
                }
                h1{
                    color: $red;
              
                    font-family: $klavika;
                    font-weight: 600;
                    
                    @include media-down(md){
                        font-size: 48px;
                        line-height: 100%;
                        margin-bottom: 40px;
                    }
                    @include media(sm){
                        font-size: 24px;
                    }
                }
                h2{
                    color: $black-dark;
               
                    font-family: $klavika;
                    font-weight: 400;
                 
                    @include media-down(md){
                        font-size: 20px;
                        padding-right: 2.5rem;
                    }
                    @include media(sm){
                        font-size: 1rem;
                    }
                }
            }
        }
    }
    &.block-intro{
        padding-top: 4rem;
        padding-bottom: 1.5rem;
        @include media(md){
            padding-top: 2.5rem;
        }
        @include media(sm){
            padding-top: 1.5rem;
            padding-bottom: .8rem;
        }
        .container-sm{
            *:last-child{
                margin-bottom: 0;
            }
            p:first-child{
       
                &::first-letter{
                    font-size: 70px;
                    float: left; /* Flotar a la izquierda para que el texto fluya alrededor */
                    margin-right: 0.1em; /* Espacio a la derecha de la letra capital */
                    line-height: .7; /* LÃ­nea base alineada */
                    font-weight: 600;
                    color: $red; /* Color opcional */
                    @include media(sm){
                        font-size: 50px;
                        line-height: .85;
                    }
                }
            }

        }
    }

    &.block-texto{
        padding: 1.5rem 0;
        @include media(sm){
            padding: .8rem 0;
        }
        .container-sm{
            *:not(h1,h2,h3,h4,h5,h6,p){
                color: $black-dark;
                font-size: 20px;
                font-weight: 400;
                line-height: 140%;
                font-family: $klavika;
                @include media(sm){
                  font-size: 18px;
                }
                @include media(xs){
                  font-size: 1rem;
                }
            }
            h1,h2,h3,h4,h5,h6{
                color: $black-dark;
                margin-bottom: 20px;
                
            }
            ul{
                list-style-position: inside;
                li{
                    margin-bottom: 5px;
                    &:last-child{
                        margin-bottom: 0;
                    }
                }
                margin-bottom: 24px;
                @include media(sm){
                    margin-bottom: 20px;
                }
            }
            *:last-child{
                margin-bottom: 0;
            }

            a{
                font-weight: bold;
                text-decoration: underline;
            }
        }
    }

    &.block-cita{
        padding: 1.5rem 0;
        @include media(sm){
            padding: .8rem 0;
        }
        .card{
            background: $white;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding-top: 45px;
            padding-bottom: 45px;
            padding-left: 77px;
            padding-right: 38px;
            position: relative;
            @include media(md){
                padding-top: 35px;
                padding-bottom: 35px;
                padding-left: 57px;
                padding-right: 28px;
            }
            @include media(sm){
                padding-top: 25px;
                padding-bottom: 25px;
    
                padding-right: 18px;
            }
            svg{
                position: absolute;
                left: -2em;
                top: 35%;
                @include media(md){
                    width: 2em;
                    left: -1em;
                    top: 25%;
                }
            }
            *:last-child{
                margin-bottom: 0;
                font-size: 36px;
                font-weight: 600;
                font-family: $klavika;
                @include media(md){
                    font-size: 24px;
                }
                @include media(sm){
                    font-size: 20px;
                }
            }
        }
    }
    &.block-imagen{
        padding: 1.5rem 0;
        @include media(sm){
            padding: .8rem 0;
        }
        .image{
            display: flex;
            flex-direction: column;
            width: 100%;
            @include media(md){
                aspect-ratio: 4 / 3; 
    
            }
            @include media(sm){
                aspect-ratio: 1 / 1;
              
            }
            picture,img{
                object-fit: cover;
                object-position: center;
                min-height: 550px;
                @include media(sm){
                    min-height: 350px;
                }
                @include media(xs){
                    min-height: 250px;
                }
            }

            figcaption{
                color: $black-dark;
                font-size: 1rem;
                border-bottom: 1px solid $black-filter;
                padding: 10px 0;
                @include media(md){
                    font-size: 14px;
                }
            }
        }
    }

}